* {
    box-sizing: border-box;
}

:root {
    --radius: 10px;
    --clr-active: rgb(240,240,240);
    --clr-shadow: rgb(130,130, 150);
    --clr-light: rgb(190,190,210);
    --clr-dark: rgb(40,40,80);
    --clr-neutral: rgb(80,80,120);
    --clr-neutral-light: rgb(100,100,140);
    --clr-neutral-dark: rgb(60,60,100);
}

#drum-machine {
    height: 100vh;
    width: 100vw;
    background-color: var(--clr-dark);
    
}

#body-container {
    background-color: var(--clr-neutral);
    display: flex;
    flex-direction: row;
    border-radius: var(--radius);
    width: 50vw;
    height: 60vh;
    margin: auto;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

#keys-container {
    box-shadow:inset 4px 4px 6px var(--clr-neutral-light);
    padding: 20px 20px 20px 20px;
    display: grid;
    grid-template-areas: "1 2 3" "4 5 6" "7 8 9";
    border-radius: var(--radius) var(--radius) var(--radius) var(--radius);
    background-color: var(--clr-neutral);
    width: 70%;
    height: 100%;
}

.drum-pad {
    box-shadow: 2px 3px var(--clr-shadow), inset 4px 4px 6px rgb(250,250,230);
    border-radius: var(--radius);
    margin: auto;
    
    min-width: 96%;
    min-height: 96%;
    background-color: var(--clr-light);
    cursor: pointer;
}

.active {
    border-radius: var(--radius);
    box-shadow: inset 0px 0px var(--clr-shadow);
    margin: auto;
    min-width: 96%;
    min-height: 96%;
    background-color: var(--clr-active);
    cursor: pointer;
}

.each-pad {
    padding-left: 10px;
    color: var(--clr-dark);
}

#display-container {
    box-shadow:inset 1px 0px var(--clr-neutral-light);
    padding: 20px;
    border-radius: 0 var(--radius) var(--radius) 0;
    background-color: var(--clr-neutral);
    width: 30%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

#display {
    padding: 3px;
    box-shadow: inset -1px 1px 4px rgb(100,100,100);
    margin-top: 4px;
    border-radius: var(--radius);
    /* background-color: rgb(160,160,200); */
    background-color: var(--clr-active);
    height: fit-content;
    width: 100%;
    text-align: center;
}

#display-text{
    color: var(--clr-dark);
}

#vol-label {
    margin-top: 40px;
    color: var(--clr-dark);
}

.slider {

    margin-top: 20px;
    margin-bottom: 20px;
    writing-mode: bt-lr; /* IE */
    -webkit-appearance: none; /* WebKit */
    background: transparent;
    border-color: transparent;
    color: transparent;
    outline: none;
    cursor: pointer;
    width: 5px;
    height: 200px;

    border: 2px solid var(--clr-neutral-dark);
    background: var(--clr-neutral-light);
}

input[type=range]::-ms-thumb {
    background: white;
    color: white;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer;
}